___CSS_LOADER_EXPORT___.push([module.id, `:root{
    --bg-black:#000000;
    --bg-orange: #FF5833FF;
    --f-red:#DC2626FF;
    --f-orange:#CA8A04FF;
    --f-green:#16A34AFF;
    --f-white:#FFFFFFFF;
    --f-grey: #8C8D8BFF;
    --b-color: #393D47FF;
    
}

body{
    margin: 0;
    height: 100vh;
    display: flex;
    flex-direction: column;
    background-color: var(--bg-black);
    font-family: Archivo;
    line-height: 1.5;
    text-transform: capitalize;
}

header{
    padding:10px 20px;
   display: flex;
   justify-content: space-between;
   align-items: center;
}
header>p{
    color: var(--f-white);
    font-size: 18px;
    font-weight: 600;
}
header button{
     background-color: var(--bg-orange);
    color: var(--f-white);
}
header button:hover{
    background-color: var(--f-white);
    color: var(--bg-orange);
}
button{
    padding: 8px 10px;
    border: none;
    border-radius: 5px;
    font: inherit;
    font-size: 12px;
    font-weight: 400;
}


.middle-content{
    flex: 1;
    display: flex;
}

.side-content{
    display: flex;
    flex-direction: column;
    gap: 5px;
}
.side-content button{
    padding: 6px 60px 6px 5px;
    border: none;
    border-radius: 5px;
    font-family: Inter;
    font-size: 12px;
}
.button-default,.add-project-button{
    background-color: var(--bg-black);
    color: var(--f-grey);
}

.button-active{
    background-color: var(--bg-orange);
    color: var(--f-white);
}

.project-buttons{
    display: flex;
    flex-direction: column;
    gap: 5px;
}
.side-content button:hover{
    background-color: var(--bg-orange);
    color: var(--f-white);
}
dialog{
    padding:10px 50px 50px 50px;
    border-radius: 10px;
}
form{
    display: flex;
    flex-direction: column;
    gap: 10px;
}
form>button{
    margin-top: 20px;

}
.button-container{
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-top: 15px;
}
.button-container>button{
flex: 1;
}
label{
    display: block;
}
.top,.middle{
    border: 1px solid var(--b-color);
    padding: 0px 20px 20px 20px;
    box-shadow: 0px 0px 1px;
    border-radius: 10px;
}
.top h1{
    color: var(--f-white);
    font-size: 36px;
}
.top p{
    color: var(--f-grey);
    font-size: 18px;
    width: 60ch;
}
.main-content{
   padding: 20px 20px;
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.edit-container{
    display: flex;
    gap: 10px;
}
.edit-container>button{
    font-size: 12px;
    padding: 8px 15px;
    border-radius: 5px;
    border: none;
}
.edit-container>*:nth-child(1){
    background-color: var(--bg-orange);
    color: var(--f-white);
}

.edit-container>*:nth-child(2){
    background-color: #282C34FF;
    color: var(--f-white);
}

input{
    border-radius: 3px;
    padding: 4px;
    border: none;
    outline-color: var(--bg-black);
    background-color:rgb(228, 228, 228);
    box-sizing: border-box;
}

.project-dialog,.project-edit-dialog{
    color: var(--f-white);
    background-color: var(--bg-black);
    border-color: var(--b-color);
}

.project-submit-button,.project-edit-submit-button{
    background-color: var(--bg-orange);
    color: var(--f-white);
}

.list-dialog,.list-edit-dialog{
    background-color: var(--b-color);
    color: var(--f-white);
    border:none;
}
.radio-container{
    display: flex;
    gap: 10px;
}
.list-form{
    gap: 10px;
}

.middle{
    color: green;
    padding: 20px;
    padding-bottom: 50px;
}
.middle>h2,.bottom>h2{
    color: var(--f-white);
    font-size: 20px;
}
.middle-container{
    display: flex;
    font-size: 36px;
    justify-content: space-evenly;
}
.middle-container>div{
    text-align: center;
}
.middle-container>div>*{
    margin: 0;
}
.middle-container>div>*:nth-child(2){
    font-size: 14px;
    text-align: center;
    color: var(--f-grey);
}

.middle-container>*:nth-child(1)>*:nth-child(1){
    color: var(--bg-orange);
}

.middle-container>*:nth-child(3)>*:nth-child(1){
    color: var(--f-red);
}
.bottom-list-container{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(390px,1fr));
    gap: 20px;
}
.bottom-list-container>div{
    display: flex;
    flex-direction: column;
    gap:10px;
    padding:20px;
    border: 1px solid var(--b-color);
}
.bottom-list-container input{
    border: 1px solid var(--bg-orange);
    border-color: var(--bg-orange);
    height: 20px;
}
.bottom-list-container>div img{
    height: 20px;
}
.bottom-list-container>div button{
    padding: 0;
    background-color: var(--bg-black);
}
.bottom-list-container div{
    color: var(--f-white);
    background-color:  #19191FFF;
    border-radius: 5px;
}

.bottom-list-container>div>*:nth-child(1){
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
}

.bottom-list-container>div>*:nth-child(1)>div{
    display: flex;
    align-items: center;
    gap: 20px;
}

.bottom-list-container>div>*:nth-child(1) h3{
    width: 15ch;    
  white-space: normal;          
  word-break: break-word;             
  margin: 0; 
}
.low{
    color: var(--f-green);
}
.medium{
    color: var(--f-orange);
}
.high{
    color: var(--f-red);
}

.date-container{
    display: flex;
   align-items: center;
   gap: 10px;
}
.date-container>img{
    height: 16px;
}
.date-container>p{
    font-size: 14px;
    color: var(--f-grey);
}
input[type="checkbox"] {
appearance: none;
  background-color: var(--bg-black);
  margin: 0;
  font: inherit;
  color: currentColor;
  width: 1.0em;
  height: 1.0em;
  border: 0.15em solid var(--f-grey);
  border-radius: 0.15em;
  transform: translateY(-0.075em);
  display: grid;
  place-content: center;
}

input[type="checkbox"]::before {
  content: "";
  width: 0.5em;
  height: 0.5em;
  transform: scale(0);
  transition: 120ms transform ease-in-out;
  box-shadow: inset 1em 1em var(--f-grey);
}

input[type="checkbox"]:checked::before {
  transform: scale(1);
}
